@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  
  width: 60%;
  margin:auto;
  background-color:inherit;
  margin-bottom: 50px; 
  margin-top: 20px;

}
.searhhBoxDiv
{
position: relative;

}
.searhhBoxDiv>img
{
position: absolute;
left: 5px;bottom: 4px;
}


.iframeDV, .iframeDV>iframe
{
  border-radius: 10px;
  margin: auto;
  text-align: center;
  font-family: "Roboto", sans-serif;

}
.iframeDV>h1{
  color: #707C8B;
}
.iframeDV>p
{
  color: #707C8B;
text-align: center;
font-size: 20px;
}

.activeBtn
{
  background-color: #5ED0C0;
  color: white;
}
.inactiveBtn
{
  background-color: #4C5D72;
  color: #9DA6B1;
}
.btn
{
  padding: 10px 25px;
  cursor: pointer;
  font-size: .9em;
  border: none;
  outline: none;
  display: inline-block;
  text-decoration: none;
}
.leftBtn {
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
  font-size: .9em;
}
.rightBtn {
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  font-size: .9em;
}


#root
{
  background-color: #2E4158;
  overflow: hidden;
  min-height: 100vh;
  

}
.header
{
  padding-left: 10px;
  margin-top: 10px;
  width: 100%;
  margin-bottom: 20px;
}
.header>button
{
  font-size: 15px;
  
}
.header input
{
  margin-top: 15px;
  font-size: 20px;
  width: 94%;
  height: 100%;
  padding-left: 30px;
  border-radius: 10px;
  background:rgba(0, 0, 0, 0.3);
  color: white;
}
.moviesOrTVShowsContainer
{
  display:flex;
  flex-wrap: wrap;
  text-align: center;
}

.container{
  height: 500px;
  text-align: center;
  margin-top: 10px;
  border-radius: 20px;
  margin-left: 20px;
  margin-right: 20px;
  color: #fff;
  flex: 1 1 40%;
  min-width: 40%;
  max-width: 50%;
  overflow: hidden;
 position: relative;
 background-repeat: no-repeat;
 background-position: center center;
background-size: 100% 100%;
 box-shadow: 0px 0px 10px 2px black;


 
}
.hiddenDiv
{
height: 100%;
width: 100%;
background-color: black;
opacity: 0.8;
display: none;
position: relative;

}

.hiddenDiv>img
{
  position: absolute;
  top:40%;

}

.container:hover .hiddenDiv
{
display: inline-block;
}

.h2div
{
  width: 100%;
  min-height: 70px;
  background-color: black;
  opacity: 0.7;
  overflow: hidden;
  border-radius: 10px;
  position: absolute;
  bottom: 0;
}

.App>h1
{
  color: #ffbed1;
}
.container:hover{
  cursor: pointer;
}
@media (max-width:750px) {
  .iframeDV>iframe {
      width: 250px;
      height: 250px;
    
  }

  .container{
    max-width: 100%;
    height: 350px;
  }
}


